<template>
  <div class="lists">
    <el-table :data="lists" border style="width: 100%" highlight-current-row @row-dblclick="select" @row-contextmenu="contextmenu">
      <el-table-column prop="ip" label="ip" width="180">
      </el-table-column>
      <el-table-column prop="name" label="" width="180">
      </el-table-column>
      <el-table-column prop="addr" label="">
      </el-table-column>
      <el-table-column prop="port" label="">
      </el-table-column>
      <el-table-column prop="ping" label="ping(ms)">
      </el-table-column>
    </el-table>
    <div id="menu" ref="meun">
      <div class="menu" @click="contextClick('delete',$event)"></div>
      <div class="menu" @click="contextClick('update',$event)"></div>
      <div class="menu" @click="contextClick('ping',$event)">ping</div>
      <div class="menu" @click="contextClick('tcping',$event)">tcp-ping</div>
    </div>
  </div>
</template>

<script>
  import {
    makeping,
    maketcping,
    _ping,
    _tcping
  } from '../utils/ping'
  export default {
    data() {
      return {
        lists: null,
        node: null,
        tr: null
      }
    },
    mounted() {
      let ipc = electron.ipcRenderer
      ipc.on('update-lists', (e, arg) => {
        this.lists = this.$global.lists = arg
      })
      ipc.on('ping', e => {
        makeping(this.lists)
      })
      ipc.on('tcp-ping', e => {
        maketcping(this.lists)
      })
      ipc.on('deleted', (e, arg) => {
        ipc.send('get-lists')
      })
    },
    activated() {
      if ((!this.lists) || (!this.$global.lists[0])) {
        let ipc = electron.ipcRenderer
        ipc.send('get-lists')
      } else
        this.lists = this.$global.lists
      this.$forceUpdate()
    },
    methods: {
      select(e, r, ele) {
        this.tr && (this.tr.style.backgroundColor = '')
        let ipc = electron.ipcRenderer
        ipc.send('change-linklist', e)
        ipc.send('close')
        this.$emit('makenow', e.name)
        this.tr = ele.target.parentElement.parentElement
        this.tr.style.backgroundColor = '#409EFF'
      },
      contextmenu(r, d, e) {
        let meun = this.$refs.meun
        // node
        this.node = r
        let length = menu.children.length
        menu.style.left = e.clientX + 'px'
        menu.style.top = e.clientY + 'px'
        menu.style.height = `${30*length}px`
      },
      contextClick(type, e) {
        e.stopPropagation()
        let ipc = electron.ipcRenderer
        let host = this.node,
          self = this,
          result
        if (type === 'delete') {
          ipc.send('delete-list', host.ip)
        } else if (type === 'update') {} else if (type === 'tcping') {
          _tcping(host, res => {
            let result = parseInt(res.avg) || parseInt(res.min) || -1
            self.lists.forEach((v, i) => {
              if (v === host) v.ping = result
            })
          })
        } else if (type === 'ping') {
          _tcping(host, res => {
            let result = parseInt(res.avg) || parseInt(res.min) || -1
            self.lists.forEach((v, i) => {
              if (v === host) v.ping = result
            })
          })
        }
        this.$refs.meun.style.height = '0'
      }
    }
  }
</script>
<style>
  .lists {
    box-shadow: 0 2px 12px 0 rgba(0, 0, 0, 0.1);
    margin: 10px;
    margin-right: 20px;
  }
  #menu {
    height: 0;
    width: 130px;
    overflow: hidden;
    box-shadow: 0 2px 4px rgba(0, 0, 0, .12), 0 0 6px rgba(0, 0, 0, .04);
    position: absolute;
    background: #fff;
    z-index: 9999;
    cursor: default;
  }
  #menu .menu {
    width: 110px;
    height: 30px;
    border-bottom: 1px solid #ccc;
    line-height: 30px;
    padding: 0 10px;
  }
  #menu .menu:hover {
    background: #ccc;
  }
</style>